<template>
  <div class="hello">
    <h1>Check out the fallen bird hat friends.</h1>
    <div class="card-group">
      <div v-for="birb of (metadata)" :key="birb.edition">
        <div class="card" style="width: 25rem; margin-left: 50px; margin-bottom: 20px">
          <img :src="birb.image" class="card-img-top" alt="Bird Hat Grift Club NFT Image"/>
          <div class="card-body">
            <h5 class="card-title">{{birb.name}}</h5>
            <p class="card-text">{{birb.description}}</p>
          </div>
          <span v-for="att of birb.attributes" :key="att" class="btn btn-primary">{{att["trait_type"]}}: {{att.value}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DeadBirbs',
  data(){
    return {
      metadata: null,
      status: null
    }
  },
  created() {
    this.fetchData()
  },
  methods: {
    async fetchData() {
      let metadataUrl = "metadata.json";
      let statusUrl = "status.json";
      this.status = await (await fetch(statusUrl)).json()
      this.metadata = (await (await fetch(metadataUrl)).json())
          .filter( x => x.edition >= this.status.starting)
          .sort((a,b)=> a.edition - b.edition);
    }
  }
}
</script>

